import isInstallApp from "@/components/dc-isInstallApp/isInstallApp.js"
export default {
	data () {
		return {
			isOn: true,
			index: 1,
			orientation: 0,
			color: 0
		}
	},
	mounted() {
		plus.key.addEventListener("backbutton",(e)=>{
			uni.showToast({
				title: JSON.stringify(e.keyType),
				icon: 'none',
				duration: 3000
			})
		});
	},
	onShow() {
		// plus.push.createMessage( '我是一条测试消息', {
		// 	payload: true
		// },{
		// 	appid: '_UNI_B73181C',
		// 	cover: true,
		// 	icon: '/static/images/hb.png',
		// 	sound: 'system',
		// 	title: '标题',
		// 	subtitle: '副标题'
		// });
	},
	methods: {
		/**
		 * 闪光灯
		 */
		open () {
			if(plus.os.name == "iOS") {  
				var avcaptClass = plus.ios.importClass("AVCaptureDevice");  
				if(avcaptClass) {  
					var device = avcaptClass.defaultDeviceWithMediaType("vide");  
					plus.ios.invoke(device, "lockForConfiguration:", null);  
					if(this.isOn) {  
						plus.ios.invoke(device, "setTorchMode:", 1);  
						plus.ios.invoke(device, "setFlashMode:", 1);  
					} else {  
						plus.ios.invoke(device, "setTorchMode:", 0);  
						plus.ios.invoke(device, "setFlashMode:", 0);  
					}  
					plus.ios.invoke(device, "unlockForConfiguration");  
				}  
			}
			this.isOn = !this.isOn;
		},
		
		/**
		 * 判断本机安装指定app
		 */
		isApp (url) {
			var isInstall = isInstallApp.isInstallApp(url);
			uni.showToast({
				title: JSON.stringify(isInstall),
				icon: 'none'
			})
		},
		
		/**
		 * 使用浏览器打开指定url
		 * plus.runtime.openURL( url, errorCB, identity );
		 * @param {String}  	url				//要打开的URL地址
		 * @param {Callback}  	errorCB			//失败回调
		 * @param {String}  	identity		//指定打开URL地址的程序名称
		 */
		openBrowser(url){
			plus.runtime.openURL(url)
		},
		
		/**
		 * 使用应用商店打开指定app
		 */
		openMarked (appurl) {
			plus.runtime.openURL(appurl, function(res) {
				console.log(res);
			});
		},
		
		/**
		 * 判断手机系统
		 */
		isPhone () {
			if (plus.os.name == 'Android') {
				plus.nativeUI.alert('Android');  
			} else if (plus.os.name == 'iOS') {
				plus.nativeUI.alert('ios');  
			} else {
				plus.nativeUI.alert('其它系统');  
			}
		},
		
		/**
		 * 获取appid
		 */
		getAppId () {
			uni.showToast({
				title: plus.runtime.appid,
				icon: 'none',
				duration: 3000
			})
		},
		
		/**
		 * 设置角标
		 * plus.runtime.setBadgeNumber(number, options);
		 * @param {number}  number 		//提示数字，0则清空
		 */
		setBadge (index) {
			if (index == 0) {
				plus.runtime.setBadgeNumber(0);
			} else {
				plus.runtime.setBadgeNumber(this.index);
				this.index++;
			}
		},
		
		/**
		 * 打开文件
		 * 
		 */
		openfile () {
			plus.runtime.openFile('/static/zsdx.pdf')
		},
		
		/**
		 * 蜂鸣
		 * plus.device.beep(times);
		 * @param {Number}  	times	//次数	
		 * 
		 */
		beep () {
			plus.device.beep(3)
		},
		
		/**
		 * 打电话
		 * plus.device.dial(number, confirm);
		 * @param {Number}  	number		//手机号码
		 * @param {Boolean}  	confirm		//true:用户确认后拨打  false:直接拨打
		 */
		dial () {
			plus.device.dial(13185712079, false);
		},
		
		/**
		 * 获取设备信息
		 */
		getInfo () {
			plus.device.getInfo({
				success:function(e){
					uni.showToast({
						title: JSON.stringify(e),
						icon: 'none',
						duration: 3000
					})
				},
				fail:function(e){
					uni.showToast({
						title: JSON.stringify(e),
						icon: 'none',
						duration: 3000
					})
				}
			});
		},
		
		/**
		 * 获取系统音量/亮度
		 */
		getVolume () {
			uni.showToast({
				title: JSON.stringify('音量：' + plus.device.getVolume() + '  亮度:' + plus.screen.getBrightness()),
				icon: 'none',
				duration: 3000
			})
		},
		
		/**
		 * 震动
		 * plus.device.vibrate(milliseconds);
		 * @param {Number}		 milliseconds		//震动时间,默认500ms 
		 */
		setvibrate () {
			plus.device.vibrate();
		},
		
		/**
		 * 获取密度/宽高
		 */
		getDpi () {
			uni.showToast({
				title: JSON.stringify('X:' + plus.screen.dpiX + '  Y:' + plus.screen.dpiY + '   W:' + plus.screen.width + '   H:' + plus.screen.height),
				icon: 'none',
				duration: 3000
			})
		},
		
		/**
		 * 锁定屏幕方向
		 */
		setLock () {
			const option = [{
				value: 'portrait-primary',
				label: '竖屏正方向'
			},{
				value: 'landscape-primary',
				label: '横屏正方向'
			},{
				value: 'landscape-secondary',
				label: ' 横屏方向'
			},{
				value: 'portrait',
				label: '竖屏正方向或反方向，根据设备重力感应器自动调整'
			},{
				value: 'landscape',
				label: '横屏正方向或反方向，根据设备重力感应器自动调整'
			}];
			plus.screen.lockOrientation(option[this.orientation].value);
			uni.showToast({
				title: JSON.stringify(option[this.orientation].label),
				icon: 'none',
				duration: 3000
			})
			this.orientation ++;
			if (this.orientation == 5) {
				this.orientation = 0;
			}
		},
		
		/**
		 * 解锁屏幕方向
		 */
		unsetLock () {
			plus.screen.unlockOrientation();
			uni.showToast({
				title: '解锁成功',
				icon: 'none',
				duration: 3000
			})
		},
		
		/**
		 * 获取网络类型
		 * CONNECTION_UNKNOW: 网络连接状态未知 0
		 *	CONNECTION_NONE: 未连接网络	1
		 *	CONNECTION_ETHERNET: 有线网络	2
		 *	CONNECTION_WIFI: 无线WIFI网络	3
		 *	CONNECTION_CELL2G: 蜂窝移动2G网络	4
		 *	CONNECTION_CELL3G: 蜂窝移动3G网络	5
		 *	CONNECTION_CELL4G: 蜂窝移动4G网络	6
		 */
		getCurrentTyoe () {
			let number = plus.networkinfo.getCurrentType();
			let option = {
				0:'网络连接状态未知',
				1:'未连接网络',
				2:'有线网络',
				3:'无线WIFI网络',
				4:'蜂窝移动2G网络',
				5:'蜂窝移动3G网络',
				6:'蜂窝移动4G网络'
			}
			uni.showToast({
				title: option[number],
				icon: 'none',
				duration: 3000
			})
		},
		
		/**
		 * 当前设备环境是否支持指纹识别
		 */ 
		getpause () {
			if (!plus.fingerprint.isSupport()) {
				uni.showToast({
					title: '不支持指纹',
					icon: 'none',
					duration: 3000
				})
			}
			
			if (plus.fingerprint.isSupport() && !plus.fingerprint.isEnrolledFingerprints()) {
				uni.showToast({
					title: '支持指纹但未录入指纹',
					icon: 'none',
					duration: 3000
				})
			}
			
			if (plus.fingerprint.isSupport() && plus.fingerprint.isEnrolledFingerprints()) {
				uni.showToast({
					title: '支持指纹并已录入指纹',
					icon: 'none',
					duration: 3000
				})
			}
		},
		
		/**
		 * 当前手机是否设置密码锁屏
		 */
		isKey () {
			uni.showToast({
				title: JSON.stringify(plus.fingerprint.isKeyguardSecure()),
				icon: 'none',
				duration: 3000
			})
		},
		
		/**
		 * 从系统相册选取文件
		 * @param {String}  	filter			//images:图片  video:视频  none:图片或视频
		 * @param {boolean}  	multiple		//true:多选  false:单选
		 * @param {Number}  	maximum			//多选数量
		 * @param {Boolean}  	system			//是否使用系统相册文件选择界面
		 */
		getPick () {
			plus.gallery.pick( (e)=>{
				let list = [];
				for(var i in e.files){
					list.push(e.files[i]);
				}
				uni.showToast({
					title: JSON.stringify(list.join()),
					icon: 'none',
					duration: 3000
				})
			},(e)=>{
				uni.showToast({
					title: '取消选择图片',
					icon: 'none',
					duration: 3000
				})
			},{
				filter: "image",
				multiple: true,
				maximum: 3,
				system: false
			});
		},
		
		/**
		 * 保存图片到相册
		 */
		savePick () {
			plus.gallery.save('http://pic.wxhand.com/student_image/1d7af20d6ffa8c9b5c3e693415a90594!Thumbwidth320',(e)=>{
				if (e.path !== '') {
					uni.showToast({
						title: '保存成功',
						icon: 'none',
						duration: 3000
					})
				}
			},(error)=>{
				uni.showToast({
					title: '保存失败',
					icon: 'none',
					duration: 3000
				})
			})
		},
		
		/**
		 * 获取设备位置信息
		 * plus.geolocation.getCurrentPosition(successCB, errorCB, option)
		 * successCB: ( GeolocationSuccessCallback ) 必选 获取设备位置信息成功回调函数
		 * errorCB: ( GeolocationErrorCallback ) 可选 获取设备位置信息失败回调函数
		 * options: ( PositionOptions ) 可选 获取设备位置信息的参数
		 */
		getPosition () {
			plus.geolocation.getCurrentPosition((e)=>{
				uni.showToast({
					title: `纬度：${e.coords.latitude},经度：${e.coords.longitude},海拔高度：${e.coords.altitudeAccuracy}`,
					icon: 'none',
					duration: 3000
				})
			}, (error)=>{
				uni.showToast({
					title: '获取失败',
					icon: 'none',
					duration: 3000
				})
			}, {
				provider:'baidu'
			})
		},
		
		/**
		 * 隐藏软键盘
		 */
		hidekeybord () {
			plus.key.hideSoftKeybord();
		},
		
		/**
		 * 发短信
		 */
		sendmessage () {
			var msg = plus.messaging.createMessage(plus.messaging.TYPE_SMS);
			msg.to = ['18611497504', '15811140520'];
			msg.body = 'This is HTML5 Plus example test message';
			plus.messaging.sendMessage( msg );
		},
		
		
		/**
		 * 系统选择按钮框
		 * NativeUIObj plus.nativeUI.actionSheet(actionsheetStyle, actionsheetCallback);
		 * actionsheetStyle: ( ActionSheetStyles ) 必选 选择按钮框显示的样式
		 *	actionsheetCallback: ( ActionSheetCallback ) 可选 选择按钮框关闭后的回调函数
		 */
		actionSheet () {
			plus.nativeUI.actionSheet(
				{
					title:"晚餐吃什么?",
					cancel:"取消",
					buttons:[
						{
							title:"烧鸡"
						},{
							title:"烤鸭"
							}
					]},
					function(e){
						let option = ['取消','烧鸡','烤鸭'];
						uni.showToast({
							title: option[e.index],
							icon: 'none',
							duration: 3000
						})
				}
			);
		},
		
		/**
		 * 	alert
		 * 	plus.nativeUI.alert(message, alertCB, title, buttonCapture);
		 * 	message: ( String ) 必选 提示对话框上显示的内容
		 *	alertCB: ( AlertCallback ) 可选 提示对话框上关闭后的回调函数
		 *	title: ( String ) 可选 提示对话框上显示的标题
		 *	buttonCapture: ( String ) 必选 提示对话框上按钮显示的内容
		 */
		alert () {
			plus.nativeUI.alert("Hello HTML5 plus!", function(){
				console.log("User pressed!");
			}, "nativeUI", "OK");
		},
		
		/**
		 * 系统确认对话框
		 * plus.nativeUI.confirm(message, confirmCB, title, buttons);	// deprecated
		 * message: ( String ) 必选 确认对话框上显示的内容
		 *	confirmCB: ( ConfirmCallback ) 可选 确认对话框关闭后的回调函数
		 *	回调函数中包括Event参数，可通过其index属性（Number类型）获取用户点击按钮的索引值。
		 *	styles|title: ( ConfirmStyles | String ) 可选 确认对话框的参数/对话框的标题
		 *	类型为json（ConfirmStyles），表示设置确认对话框的参数； 类型为String时，表示设置确认对话框的标题（不推荐使用）。
		 *	buttons: ( Array[ String ] ) 可选 确认对话框上显示的按钮（不推荐使用）
		 *	字符串数组，每项对应在确认对话框上显示一个按钮，用户点击后通过confirmCB返回用户点击按钮的在数组中的索引值。 不推荐使用，建议使用styles参数中的buttons属性。
		 */
		confirm () {
			let option = ['确认','取消'];
			plus.nativeUI.confirm("Are you sure ready?", function(e){
				uni.showToast({
					title: option[e.index],
					icon: 'none',
					duration: 3000
				})
			});
		},
		
		/**
		 * 预览图片
		 * plus.nativeUI.previewImage(urls, styles);
		 * @param {String}  	urls		//图片地址，支持网络地址和本地地址
		 * @param {Object}  	styles		//图片样式
		 * background: (String 类型 )图片预览的背景颜色
			颜色值格式为"#RRGGBB"，如"#FF0000"表示为红色背景。 默认值为黑色（"#000000"）。
		 * 	current: (Number 类型 )默认显示图片的索引值
			索引值从0开始，默认值为0。
		 *	indicator: (String 类型 )图片指示器样式
			可取值： "default" - 默认指示器（底部圆点样式）； "number" - 顶部数字指示器（顶部居中显示，文字为%当前图片索引值（从1开始）%/%图片总数%）； "none" - 不显示指示器。 默认值为"default"。
		 *	loop: (Boolean 类型 )是否可循环预览
			可取值： "true" - 支持循环预览； "false" - 不支持循环预览。 默认值为"false"。
		 *	onLongPress: (PreviewImageLongPressCallback 类型 )长按事件
			用户在图片预览界面长按时触发。 在回调中返回长按图片的信息（索引值、地址等）。 由于图片预览界面是层级非常高的窗口，此时只能调用plus.nativeUI.*显示的窗口覆盖在图片预览界面上（原生控件plus.nativeObj.View也无法显示）。 注意：HBuilderX1.9.5及以上版本支持。
		 * 
		 */
		preview () {
			plus.nativeUI.previewImage([
				'http://pic.wxhand.com/student_image/1d7af20d6ffa8c9b5c3e693415a90594!Thumbwidth320',
				'http://pic.wxhand.com/student_image/1d7af20d6ffa8c9b5c3e693415a90594!Thumbwidth320',
				'http://pic.wxhand.com/student_image/1d7af20d6ffa8c9b5c3e693415a90594!Thumbwidth320',
				'http://pic.wxhand.com/student_image/1d7af20d6ffa8c9b5c3e693415a90594!Thumbwidth320'
			],{
				current:1,
				loop:true,
				onLongPress:function(e){	// 预览界面长按显示ActionSheet
					var bts=[
						{title:"随便来个标题",style:"destructive"},
						{title:"测试按钮一"},
						{title:"测试按钮二"}
					];
					plus.nativeUI.actionSheet({
						title:"ActionSheet标题",
						cancel:"取消",
						buttons:bts,
					},
						function(e){
							let title = e.index > 0 ? bts[e.index-1].title : "取消";
							uni.showToast({
								title: title,
								icon: 'none',
								duration: 3000
							})
						}
					);
				}
			});
		},
		
		/**
		 * 时间选择对话框
		 */
		PickTimeStyles () {
			var styles = {};
			styles.title = "请选择日期："; // 显示标题
			styles.date = new Date();// 默认显示的日期
			
			plus.nativeUI.pickDate(function(e){
				var d = e.date;
				let date = d.getFullYear()+"-"+(d.getMonth()+1)+"-"+d.getDate();
				uni.showToast({
					title: date,
					icon: 'none',
					duration: 3000
				})
			}, function(e){
				uni.showToast({
					title: e.message,
					icon: 'none',
					duration: 3000
				})
			}, styles);
		},
		
		/**
		 * 自动关闭对话框
		 */
		closemodule () {
			var w = plus.nativeUI.showWaiting( "等待中..." );
			// 2秒后关闭
			setTimeout(function(){
				w.close();
			}, 2000);
		},
		
		/**
		 * 是否是刘海屏
		 */
		hasNotchInScreen () {
			uni.showToast({
				title: JSON.stringify(plus.navigator.hasNotchInScreen()),
				icon: 'none',
				duration: 3000
			})
		},
		
		/**
		 * 创建快捷方式
		 * plus.navigator.createShortcut(options, successCallback);
		 */
		createShortcut () {
			plus.navigator.createShortcut({
				name: '测试快捷',
				toast: '创建成功'
			});
		},
		
		/**
		 * 获取系统状态栏颜色
		 */
		getStatusBarBackground () {
			uni.showToast({
				title: JSON.stringify(plus.navigator.getStatusBarBackground()),
				icon: 'none',
				duration: 3000
			})
		},
		
		/**
		 * 设置系统状态栏颜色
		 */
		setStatusBarBackground () {
			let color = this.color % 2 == 0 ? 'dark' : 'light';
			this.color++;
			plus.navigator.setStatusBarStyle(color);
		},
		
		/**
		 * 获取系统状态栏高度
		 */
		getStatusbarHeight () {
			uni.showToast({
				title: JSON.stringify(plus.navigator.getStatusbarHeight()),
				icon: 'none',
				duration: 3000
			})
		},
		
		/**
		 * 获取支付通道
		 * plus.payment.getChannels(successCB, errorCB);
		 */
		getChannels () {
			plus.payment.getChannels((e)=>{
				uni.showToast({
					title: JSON.stringify(e),
					icon: 'none',
					duration: 3000
				})
			});
		},
		
		
		/**
		 * 启动语音识别
		 */
		startRecognize () {
			plus.speech.startRecognize({
				continue: false,
				engine: 'baidu',
				lang: 'zh-cn',
				nbest: 1,
				timeout: 1000,
				userInterface: true,
				
			}, (res)=>{
				uni.showToast({
					title: JSON.stringify(res),
					icon: 'none',
					duration: 3000
				})
			}, (error)=>{
				uni.showToast({
					title: JSON.stringify(error),
					icon: 'none',
					duration: 3000
				})
			} );
			
			var text = null;
			plus.speech.addEventListener("start", function(){
				text = null;
			}, false);
			plus.speech.addEventListener("recognition", function(e){
				text += e.result;
			}, false);
			plus.speech.addEventListener("end", function(){
				alert("Success: "+text);
			}, false);
		},
		
		scanCode() {
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
			    }
			});
		}
	}
}
